<template>
  <div>
    <div class="top">
      <!--<div class="head">
        <span>猴小瘦</span>
        <i></i>
      </div>-->
      <div class="title">{{projectinfo.title}}</div>
      <div class="category">
        <ul>
          <li>{{projectinfo.category}}</li>
        </ul>
      </div>
      <div class="time">{{projectinfo.up_time}}</div>
      <div class="desc">{{projectinfo.description}}</div>

    </div>
    <div class="work">
      <div class="worklist">
        <ul>
          <li @click="showphoto(item)" v-for="item in projectinfo.works"><img :src="item" alt=""></li>
        </ul>
      </div>
    </div>

    <!--<div id="mymodal" class="mymodal" v-show="modelshow">
      &lt;!&ndash;模态框&ndash;&gt;
      <div class="conte">
        <div style="width: 100%;line-height: 30px;height: 39px;"><span
          style="float: right;cursor: pointer"
          @click="modelshow=false"><i class="iconfontyyy">&#xe67c;</i></span>
        </div>
        <img id="showphoto" width="500px">

      </div>
    </div>-->

  </div>
</template>
<script>


  function categorychange(type) {
    var type = type;
    if (type == 1) {
      type = "PPT";
    } else if (type == 2) {
      type = "UI";
    } else if (type == 3) {
      type = "文本画册";
    } else if (type == 4) {
      type = "海报展览";
    } else if (type == 5) {
      type = "LOGO";
    } else if (type == 6) {
      type = "企业形象设计（VI）";
    } else if (type == 0) {
      type = "测试品类";
    }
    return type;
  }


  export default{
    name: 'project',
    data(){
      return {
        projectinfo: {},
        modelshow: false,
      }
    },
    created(){
      if (this.$route.params.id) {
        this.getingf(this.$route.params.id);
      } else {
        location.href = "http://houxiaopang.com/404";
      }
    },
    mounted(){
      //this.preventDefault();
    },
    methods: {
      showphoto(src){
        //this.modelshow = true;
        //document.getElementById("showphoto").src = src;
        open(src,'图片预览');
      },
       /*preventDefault(){
        document.getElementById("mymodal").onwheel = function (e) {
          e.preventDefault();
        }
      },*/
      getingf(id){
        var that = this;
        $.ajax({
          type: "GET",
          url: "http://houxiaopang.com/api/v1.1/albumdetail",
          data: {'album_id': id},
          success(result){
            if (result.code == 0) {
              that.projectinfo = result.data;
              that.projectinfo.category = categorychange(that.projectinfo.category);
            } else {
              location.href = "http://houxiaopang.com/404";
            }
          }
        });
      }
    }
  }
</script>
<style>
  .top {
    width: 1000px;
    margin: 0 auto;
    position: relative;
  }

  .top > .head {
    position: absolute;
    top: 0;
    right: 0;
  }

  .top > .head > i {
    display: inline-block;
    width: 50px;
    height: 50px;
    background: url("../assets/touxiang.png");
    background-size: cover;
  }

  .top > .head > span {

  }

  .title {
    font-size: 32px;
    font-weight: 700;
    line-height: 130px;

  }

  .desc {
    color: #313131;
    font-size: 20px;
    line-height: 30px;
    clear: both;
    margin-top: 72px;
  }

  .time {
    font-size: 15px;
    color: #969899;
    line-height: 30px;
    float: right;
  }

  .category {
    float: left;
  }

  .category > ul {
    list-style: none;
    overflow: hidden;
  }

  .category > ul > li {
    width: auto;
    padding: 0 20px;
    text-align: center;
    height: 30px;
    line-height: 30px;
    border-radius: 25px;
    float: left;
    border: 1px solid #dcdee0;;
    margin-right: 25px;
    color: #969899;
  }

  .work {
    width: 100%;
    background: #f8f9fa;
    overflow: hidden;
    margin-top: 120px;
    padding-top: 50px;
  }

  .worklist {
    width: 1000px;
    margin: 0 auto;
  }

  .worklist > ul {
    list-style: none;
  }

  .worklist ul li {
    float: left;
    width: 450px;
    margin-bottom: 50px;
    margin-right: 25px;
    cursor: pointer;
  }

  .worklist li img {
    width: 100%;
    box-shadow: 3px 3px 5px 1px #999;
  }

  .mymodal {
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: rgba(0, 0, 0, 0.6);
    z-index: 20;
  }

  .mymodal > .conte {
    margin: 30px auto;
    overflow: hidden;
    border-radius: 5px;
    padding: 15px;
  }

  .iconfontyyy {
    font-family: "iconfont", serif !important;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -webkit-text-stroke-width: 0.2px;
    font-size: 24px;
    background: transparent;
    color: #FFF;
  }
</style>



