import Vue from 'vue'
import Router from 'vue-router'
import project from '@/components/projectinfo'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/:id',
      name: 'project',
      component: project
    },
    {
      path: '/',
      name: 'project',
      component: project,
    }
  ]
})



